<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process38"></a>

<p><br></p>
<p><span class="rvts17">巫师（</span><span class="rvts50">Wizard</span><span class="rvts17">）</span></p>
<p><img width="422" height="723" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber127.jpg"><span class="rvts48"></span></p>
<p><span class="rvts43"></span>
<br><span class="rvts21">在原罪</span><span class="rvts20">2</span><span class="rvts21">中，女巫其实并不是纯粹的法系输出单位。她和战法一样，也拥有一定的近战作战能力。然而不同的是，女巫的近战是以</span><span class="rvts20"></span><span class="rvts21">敏捷</span><span class="rvts20"></span><span class="rvts21">型爆发伤害体现出来的。</span>
<br><span class="rvts18"><br></span></p>
<div><table width="979" border="1" cellpadding="0" cellspacing="-1" style="border-width: 0px; border-collapse: collapse;">
<tr valign="top">
<td width="262" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">主属性</span></p>
</td>
<td width="165" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">主派系</span></p>
</td>
<td width="161" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">默认技能</span><span class="rvts27">1</span></p>
</td>
<td width="161" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">默认技能</span><span class="rvts27">2</span></p>
</td>
<td width="161" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">默认技能</span><span class="rvts27">3</span></p>
</td>
</tr>
<tr valign="top">
<td width="262" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">灵巧，智力，体质</span></p>
</td>
<td width="165" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">恶棍，死灵</span></p>
</td>
<td width="161" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="64" height="64" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber128.jpg"><span class="rvts39"></span>
<br><span class="rvts21">浮肿尸体</span></p>
</td>
<td width="161" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="64" height="64" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber129.jpg"><span class="rvts39"></span>
<br><span class="rvts21">三氯甲烷</span></p>
</td>
<td width="161" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="64" height="64" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber130.jpg"><span class="rvts39"></span>
<br><span class="rvts21">虫群</span></p>
</td>
</tr>
</table>
</div>
<p class="rvps2"><span class="rvts28"><br></span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



